import React from "react";
import { FaBars } from "react-icons/fa";
import {
  Nav,
  NavBarContiner,
  NavLogo,
  NavMenu,
  NavItem,
  NavLinks,
  MobileIcon,
} from "./NavbarElements";

const Navbar = () => {
  return (
    <>
      <Nav>
        <NavBarContiner>
          <NavLogo to="/">Mega Js</NavLogo>
          <MobileIcon>
            <FaBars />
          </MobileIcon>
          <NavMenu>
            <NavItem>
              <NavLinks to="/">Home</NavLinks>
            </NavItem>
            <NavItem>
              <NavLinks to="/courses">Courses</NavLinks>
            </NavItem>
            <NavItem>
              <NavLinks to="/blog">Blog</NavLinks>
            </NavItem>
            <NavItem>
              <NavLinks to="/roadmap">Road Map</NavLinks>
            </NavItem>
          </NavMenu>
        </NavBarContiner>
      </Nav>
    </>
  );
};
export default Navbar;
